/**
 * Quiz Data Models
 * Định nghĩa cấu trúc dữ liệu cho bài test và nhóm bài học
 */

/**
 * Quiz Group Types
 */
export const QUIZ_GROUP_TYPES = {
  OFFICIAL: 'official',      // Nhóm chính thức (admin)
  COMMUNITY: 'community',    // Nhóm cộng đồng (người dùng chia sẻ)
  PERSONAL: 'personal',      // Nhóm cá nhân (riêng người dùng)
};

/**
 * Quiz Source Types
 */
export const QUIZ_SOURCE_TYPES = {
  GOOGLE_SHEET: 'google_sheet',  // Load từ Google Sheet
  DATABASE: 'database',           // Đã cache vào database
  LOCAL: 'local',                 // File local
};

/**
 * Quiz Metadata
 * Thông tin tổng quan về một bài test
 */
export const createQuizMetadata = ({
  id,
  title,
  description,
  levelId,          // NEW: Education level ID
  subjectId,         // NEW: Subject ID (changed from subject)
  quizType,          // NEW: Changed from group to quizType
  source,
  sourceUrl = null,
  questionCount = 0,
  creatorId = null,
  creatorName = null,
  createdAt = Date.now(),
  updatedAt = Date.now(),
  usageCount = 0,
  rating = 0,
  tags = [],
  difficulty = 'medium',
  estimatedTime = 0,
  isPublic = false,
  isVerified = false,
}) => ({
  id,
  title,
  description,
  levelId,           // 'preschool', 'grade1', 'grade3', 'general', etc.
  subjectId,        // 'math', 'vietnamese', 'english', 'ielts', etc.
  quizType,         // 'official', 'community', 'personal'
  source,           // 'google_sheet', 'database', 'local'
  sourceUrl,        // URL Google Sheet hoặc path
  questionCount,
  creatorId,
  creatorName,
  createdAt,
  updatedAt,
  usageCount,
  rating,
  tags,
  difficulty,       // 'easy', 'medium', 'hard'
  estimatedTime,    // Thời gian ước tính (phút)
  isPublic,         // Có công khai không (cho community)
  isVerified,       // Đã được admin verify chưa
  // Backward compatibility
  subject: subjectId,
  group: quizType,
});

/**
 * Quiz Data
 * Dữ liệu đầy đủ của một bài test (bao gồm câu hỏi)
 */
export const createQuizData = ({
  metadata,
  questions = [],
  cachedAt = null,
}) => ({
  ...metadata,
  questions,
  cachedAt,         // Thời điểm cache vào database
});

/**
 * Subject Configuration
 * Cấu hình môn học trong một nhóm
 */
export const createSubjectConfig = ({
  id,
  name,
  description,
  icon,
  group,
  order = 0,
  isActive = true,
}) => ({
  id,
  name,
  description,
  icon,
  group,
  order,
  isActive,
});

